<template>
  <div>
    <Row class="header-row">
      <Col
        span="24"
        style="max-width:calc(100% - 230px);cursor: pointer;"
      >
    
      </Col>
      <Col
        span="24"
        style="max-width:250px;cursor: pointer;"
      >
      <Badge
        dot
        style="display: initial;"
      >
      <Dropdown>
        <Icon
          type="ios-notifications-outline"
          size="20"
        ></Icon>
        <DropdownMenu slot="list">
          <DropdownItem v-for="info in infoList" :key="info.id">
            <Row style="width:200px">
              <Col span="8">{{info.title}}</Col>
              <Col span="8" offset="8">{{info.date}}</Col>
            </Row>
          </DropdownItem>
        </DropdownMenu>
      </Dropdown>
      </Badge>
      <Divider type="vertical" />
      <span>用户名</span>
      <Avatar
        icon="ios-person"
        class="header-Avatar"
        size="40"
      />
      <span class="header-span">用户身份</span>
      <Divider type="vertical" />
      <Tooltip
        content="退出"
        theme="light"
      >
        <Icon
          type="md-log-out"
          size="16"
          @click="outLogin"
        />
      </Tooltip>
      </Col>
    </Row>
  </div>
</template>
<script>
export default {
  name: "Header",
  data() {
    return {
      classShow: false,
      infoList:[
        {
          id:1,
          title:"通知一",
          date:"2020-08-09"
        },
        {
          id:2,
          title:"通知二",
          date:"2020-09-11"
        }
      ]
    };
  },
  methods: {
    outLogin() {
      this.$router.replace({
        path: "/login",
      });
    },
    classClick() {
      this.classShow = !this.classShow;
      this.$emit("fatherMethod");
      if (this.classShow) {
        this.$store.commit("avatarSizeChange", "70");
        document.getElementsByClassName("menu-log")[0].style.height = "120px";
        document.getElementsByClassName("my-menu")[0].style.height =
          "calc(100% - 120px)";
        document.getElementsByClassName("ivu-layout-sider")[0].style.width =
          "80px";
        document.getElementsByClassName("ivu-layout-sider")[0].style.minWidth =
          "80px";
        document.getElementsByClassName("ivu-layout-sider")[0].style.maxWidth =
          "80px";
      } else {
        this.$store.commit("avatarSizeChange", "100");
        document.getElementsByClassName("menu-log")[0].style.height = "180px";
        document.getElementsByClassName("my-menu")[0].style.height =
          "calc(100% - 180px)";
        document.getElementsByClassName("ivu-layout-sider")[0].style.width =
          "200px";
        document.getElementsByClassName("ivu-layout-sider")[0].style.minWidth =
          "200px";
        document.getElementsByClassName("ivu-layout-sider")[0].style.maxWidth =
          "200px";
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.myClass {
  transform: rotate(90deg);
}
</style>