<template>
<div>
    <ButtonGroup>
        <Button @click="addFlowModal=true">新增培养流程</Button>
        <Button >导入培养流程</Button>
    </ButtonGroup>
    <br>
    <Card>
        <Table :columns="columns" :data="tableData">
            <div slot-scope="{row}" slot="action">
                <Button type="primary" size="small" style="margin-right: 5px" @click="edit(row)">Edit</Button>
            <Button type="error" size="small" @click="remove(row)">Delete</Button>
            </div>

        </Table>
    </Card>
    
    <Modal v-model="addFlowModal" @on-ok="addFlow">
        <Form :model="newStuFlow">
        <FormItem label="流程名称" prop="flow_name">
            <Input v-model="newStuFlow.flow_name"></Input>
        </FormItem>
        <FormItem label="适用学生类型" prop="stu_type">
            <Select v-model="newStuFlow.stu_type"></Select>
        </FormItem>
         <FormItem label="适用学生学制" prop="stu_type">
            <Select v-model="newStuFlow.stu_type"></Select>
        </FormItem>
        <FormItem label="预估时间" prop="predict_time">
            <Input v-model="newStuFlow.predict_time"></Input>
        </FormItem> 
        <FormItem label="实际时间" prop="real_time">
            <DatePicker type="date" format="yyyy-MM-dd" :value="newStuFlow.real_time"></DatePicker>
        </FormItem>            
        </Form> 
    </Modal>
</div>
</template>
<script>
export default {
    data () {
        return {
            currentId: '',
            currentScore: '',
            addFlowModal:false,
            newStuFlow:{
                stu_type:"",
                flow_name:"",
                predict_time:"",
                real_time:""
            },
            columns: [
                { title: '流程名称', key: 'flow_name', align: 'center' },
                {
                    title: '适用学生类型',
                    key: 'stu_type',
                    align: 'center'
                
                },
                {
                    title:"适用年级",
                    key:'stu_grade',
                    align:'center'
                },
                {
                    title:"适用学制",
                    key:'duration',
                    align:'center'
                },
                {
                    title:'预估时间',
                    key:'predict_time'
                },
                {
                    title:'实际时间',
                    key:'real_time'

                },
                {
                title: '操作',
                align: 'center',
                slot: "action"
  
                }
            ],
            tableData: [
                { 
                  id: 1,
                  flow_name: '开题报告',
                  stu_type: '普通博士',
                  stu_grade:"二年级",
                  duration:'4年',
                  predict_time: '第四学期',
                  real_time: '2021-04-20',
                },
                { 
                  id: 2, 
                  flow_name: '开题报告',
                  stu_type: '本科直博',
                  stu_grade:'三年级',
                  duration:'5年',
                  predict_time: '第六学期',
                  real_time: '2021-04-20',
                },
                {
                    id:3,
                    flow_name:'开题报告',
                    stu_type: '学硕、专硕(全日制)',
                    stu_grade:'三年级',
                    duration:'3年',
                    predict_time: '第三学期11月第三个星期四',
                    real_time: '2021-11-23',
                }
            ]
        }
    },
 
    methods: {
        handleSave (id) {
            const {currentScore, tableData} = this
            this.tableData = tableData.map(v => {
                return v.id === id ? { ...v, score: currentScore } : v
            })
            this.currentId = ''
            this.currentScore = ''
        },
        addFlow(){
            this.tableData.push(Object.create(this.newStuFlow))
            console.log(this.newStuFlow)
        }
    }
}
</script>